@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
@import url(https://fonts.googleapis.com/css?family=Lato:300,400,700);
@import url(https://maxcdn.bootstrapcdn.com/bootstrap/4.3.1/css/bootstrap.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.wrapper{background:black;padding:65px;min-height:100vh}.header-row{padding:20px;text-align:center;background-color:#d3d3d3;margin-bottom:10px}.refresh-button-panel{text-align:center}.refresh-button-panel i{position:absolute;display:none}.refresh-button-panel button{background-color:#d3d3d3;border:0px;color:black;border-radius:10px;transition:0.4s;border:0}.refresh-button-panel button:hover{background-color:#00ccff81;color:#ffffff;border-radius:30px}.data-panel{margin:0px 20px;text-align:center;color:grey}.data-panel .header{margin-top:20px;font-weight:600;font-size:28px;text-align:center}.data-panel .footer{margin-top:20px;text-align:center}.data-panel .footer p{font-size:16px}.wrapper-box{padding:15px}

